import { Component, Input, OnInit } from '@angular/core';
import { Store } from '@ngxs/store';
import { ChooserResult } from '@ionic-native/chooser/ngx';
import { ModalController } from '@ionic/angular';
import { UserState } from '../../../@store/user/user.state';
import { IUserInfo } from '../../../@store/user/user.model';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Constants } from '../../../@global/constants';
import { FormCustomValidators } from '../../validators/form-custom-validators';
import { UtilsService } from '../../../@core/utils.service';
import { InformService } from '../../../@core/inform.service';

@Component({
  selector: 'app-request-message-modal',
  templateUrl: './request-message-modal.component.html',
  styleUrls: ['./request-message-modal.component.scss'],
})
export class RequestMessageModalComponent implements OnInit {
  @Input() type: string;
  public formGroup: FormGroup;
  public userInfo: IUserInfo;
  public isAcademic = false;
  public maxDescriptionSize = Constants.MAX_PUBLICATION_DESCRIPTION_SIZE;
  public maxlengthName = Constants.MAX_LENGTH_NAME_COGNITO;
  public resume: ChooserResult;

  constructor(
    private store: Store,
    private formBuilder: FormBuilder,
    private modalController: ModalController,
    private informService: InformService,
    protected utilsService: UtilsService
  ) {}

  ngOnInit() {
    if (this.type === 'academic') {
      this.isAcademic = true;
    }
    this.userInfo = this.store.selectSnapshot(UserState.getUserInfo);
    this.formGroup = this.formBuilder.group({
      name: [
        this.userInfo.name,
        [
          Validators.required,
          Validators.maxLength(this.maxlengthName),
          FormCustomValidators.notAllowedEmojis,
          FormCustomValidators.notAllowedNumbers,
        ],
      ],
      surname: [
        this.userInfo.surname,
        [
          Validators.required,
          Validators.maxLength(this.maxlengthName),
          FormCustomValidators.notAllowedEmojis,
          FormCustomValidators.notAllowedNumbers,
        ],
      ],
      message: ['', [Validators.maxLength(this.maxDescriptionSize)]],
      contactEmail: [
        this.userInfo.contact_email || '',
        [Validators.email, Validators.maxLength(Constants.MAX_LENGTH_USERNAME_COGNITO)],
      ],
      contactPhone: [this.userInfo.contact_phone || '', [Validators.maxLength(Constants.MAX_LENGTH_USERNAME_COGNITO)]],
    });
  }

  public async goBack() {
    await this.modalController.dismiss();
  }

  public async send() {
    if (!this.utilsService.isValidForm(this.formGroup)) {
      return;
    }
    if (
      this.formGroup.get('contactEmail').value.trim() === '' &&
      this.formGroup.get('contactPhone').value.trim() === ''
    ) {
      this.informService.showWarning('requestModal.warning');
      return;
    }
    await this.modalController.dismiss({
      name: this.formGroup.get('name').value,
      surname: this.formGroup.get('surname').value,
      message: this.formGroup.get('message').value,
      contactEmail: this.formGroup.get('contactEmail').value,
      contactPhone: this.formGroup.get('contactPhone').value,
      resume: this.isAcademic ? null : this.resume,
    });
  }

  public async selectPDF() {
    try {
      this.resume = await this.utilsService.selectPDF();
    } catch (error) {
      throw new Error(error);
    }
  }
}
